html {
    overflow-y: scroll;
  }

:root {
  --citrus-bone: rgb(245,248,207);
  --citrus-red: rgb(198,23,23);
  --citrus-bright-red: rgb(239, 7, 7)
}

@font-face {
    font-family: 'barloesius-schriftregular';
    font-style: normal;
    /* font-weight: 400; */
    src: local('barloesius-schriftregular'), local('barloesius-schriftregular'),
    url('./fonts/barloesius-schrift/barloesius-schrift.regular-webfont.woff')
    format('woff'); 
  }
@font-face {
    font-family: 'drywhiteboardmarker-regular-webfont';
    font-style: normal;
    /* font-weight: 400; */
    src: local('drywhiteboardmarker-regular-webfont'),
    local('drywhiteboardmarker-regular-webfont'),
    url('./fonts/webfont-kits/drywhiteboardmarker-regular-webfont.woff')
    format('woff'); 
  }

@font-face {
    font-family: 'x_bambi-webfont';
    font-style: normal;
    /* font-weight: 400; */
    src: local('x_bambi-webfont'),
    local('x_bambi-webfont'),
    url('./fonts/webfont-kits/x_bambi-webfont.woff')
    format('woff'); 
  }

body{
    background-color: black;
    margin: 0;
}

.App body {
  padding-left: 20%;
  padding-right: 20%;
}

.center {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.navSelected, .navUnselected {
  font-size: 36px;
  font-family: 'barloesius-schriftregular';
  color: var(--citrus-bone);
  text-decoration: none;
}

p {
  font-family: 'x_bambi-webfont';
  font-size: 18px;
  color: rgb(245, 248, 207);
  animation-name: p-color;
  animation-iteration-count: infinite;
  animation-duration: 1.75s;
  animation-direction: alternate;
}

@keyframes p-color{
  from {color:var(--citrus-bone);}
  to {color:rgb(210, 212, 179);}
}

.navSelected {
  text-decoration: underline;
  color: var(--citrus-red);
  animation-name: selected-color;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes selected-color {
  from {color:var(--citrus-bone);}
  to {color: var(--citrus-red);}
}

/* The element to apply the animation to */
/* div {
  width: 100px;
  height: 100px;
  background-color: red;
  animation-name: example;
  animation-duration: 4s;
} */
.nav-background {
  width: 100%;
  position: fixed;
  background-color: rgba(53, 0, 0, 0.9)
}

.nav-container {
  display: flex;
  justify-content: center;
}

.nav-item {
  margin: 10px;
  margin-left: 15px;
  margin-right: 15px;
}

.page-content{
  padding-top: calc(36px + 20px);
}

.center p {
  text-align: center;
}

a {
  color: white;
}

.devul-animation {
    margin: auto;
    margin-bottom: 0;
    height: 240px;
    width: 240px;
    background: url("./images/devul-sprite-sheet.png");
    animation-name: sprite;
    animation-duration: 5s;
    animation-timing-function: steps(249); 
    /* Set steps to 250 for fun animation lmao */
    animation-iteration-count: infinite;
    
}

@keyframes sprite {
    from {background-position: 0px;}
    to {background-position: 59760px;}
}

